body .is-layout-constrained .wp-block-blocks-gamestore-block-hero {
	position: relative;
	max-width: none;
	margin-right: calc(var(--wp--style--root--padding-right) * -1) !important;
	margin-left: calc(var(--wp--style--root--padding-left) * -1) !important;
	.hero-section {
		width: 100% !important;
		height: 980px;
		max-width: 100vw !important;
		margin: 0;
	}
}
.wp-block-blocks-gamestore-block-hero {
	.image-bg {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
		z-index: 0;
	}
	.video-bg {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
		z-index: 0;
	}

	.hero-mask {
		width: 80%;
		height: 100%;
		display: block;
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		z-index: 1;
		background: var(--mask-gradient);
	}

	.hero-content {
		max-width: 1540px;
		margin: 0 auto;
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		justify-content: center;
		gap: 24px;
		position: relative;
		z-index: 5;
		padding-left: var(--wp--style--root--padding-left);
		padding-right: var(--wp--style--root--padding-right);

		.hero-title {
			color: var(--white-contrast);
			font-size: 64px;
			line-height: 120%;

			strong {
				background: var(
					--accent-gradient,
					linear-gradient(90deg, #3dafe2 0%, #6f76c0 100%)
				);
				background-clip: text;
				-webkit-background-clip: text;
				-webkit-text-fill-color: transparent;
			}
		}
		.hero-description {
			font-size: 20px;
			line-height: 160%;
		}
	}

	.hero-slider {
		width: 100%;
		position: absolute;
		left: 0;
		bottom: 0;
		height: 150px;
		display: flex;
		align-items: center;
		justify-content: center;
		background: var(
			--mask-gradient-second,
			linear-gradient(270deg, rgba(250, 250, 250, 0) 0%, #fafafa 100%)
		);

		.slider-container {
			.swiper-wrapper {
				.slide-item {
					max-width: fit-content;

					img.dark-logo {
						display: none;
					}
				}
			}
		}
	}
}

.dark-mode-gamestore {
	.wp-block-blocks-gamestore-block-hero
		.hero-slider
		.slider-container
		.swiper-wrapper
		.slide-item {
		img.dark-logo {
			display: block;
		}
		img.light-logo {
			display: none;
		}
	}
}

@media (max-width: 1440px) {
	body .is-layout-constrained .wp-block-blocks-gamestore-block-hero {
		.hero-section {
			height: 810px;
		}
	}
	.wp-block-blocks-gamestore-block-hero {
		.hero-content {
			.hero-title {
				font-size: 54px;
			}
			.hero-description {
				font-size: 18px;
			}
		}
		.hero-slider {
			height: 120px;
		}
	}
}
@media (max-width: 1199px) {
	.wp-block-blocks-gamestore-block-hero {
		.hero-slider {
			.slider-container {
				.swiper-wrapper {
					.slide-item img {
						max-height: 40px;
					}
				}
			}
		}
	}
}

@media (max-width: 991px) {
	.wp-block-blocks-gamestore-block-hero {
		.hero-content {
			.hero-title {
				font-size: 44px;
			}
		}
	}
}

@media (max-width: 768px) {
	.wp-block-blocks-gamestore-block-hero {
		.hero-content {
			gap: 18px;

			.hero-description {
				font-size: 16px;
			}
			.hero-title {
				font-size: 38px;
			}
		}
	}
}

@media (max-width: 480px) {
	body .is-layout-constrained .wp-block-blocks-gamestore-block-hero {
		.hero-section {
			min-height: 100svh;
			height: auto;
		}
	}
	.wp-block-blocks-gamestore-block-hero {
		.hero-content {
			padding: 80px 16px 200px;
			.hero-title {
				font-size: 38px;
				max-width: 300px;
			}
			.hero-description {
				font-size: 14px;
				max-width: 256px;
			}
		}
		.hero-slider {
			height: 62px;
		}
		.hero-mask {
			width: 100%;
		}
	}
}
@media (max-width: 340px) {
	.wp-block-blocks-gamestore-block-header {
		.custom-logo-link {
			width: 120px;
			height: 18px;
		}
	}
}
